const initialState = {
  isAuthenticated: false,
  user: {}
};

export default function(state = initialState, action) {
  switch (action.type) {
    case "":
      return {
        ...state,
        //the payload we return from authActions.js from setCurrentUser has the decoded data that has user ,we will check if it is not empty,so if that object(decoded in authActions.js) returned(payload) is not empty we should be authenticated if empty we should not be authenticated
        isAuthenticated: false,
        user: action.payload
      };

    default:
      return state;
  }
}
