//createStore ,Creates a Redux store that holds the complete state tree of your app.There should only be a single store in your app
//The key feature of middleware is that it is composable. Multiple middleware can be combined together, where each middleware requires no knowledge of what comes before or after it in the chain.
//compose allows to compose our set of  enhancers ,a middleware is also an enhancer.
import { createStore, applyMiddleware, compose } from "redux";
//Thunk middleware for Redux.Thunks are the recommended middleware for basic Redux side effects logic, including complex synchronous logic that needs access to the store, and simple async logic like AJAX requests.Redux Thunk middleware allows you to write action creators that return a function instead of an action. The thunk can be used to delay the dispatch of an action, or to dispatch only if a certain condition is met.since the thunk is a middleware we use 'applyMiddleware' function of redux
import thunk from "redux-thunk";
//This is where we combined all of our reducers
import rootReducer from "./reducers/index";

//A store holds the whole state tree of your application. The only way to change the state inside it is to dispatch an action on it.A store is not a class. It's just an object with a few methods on it. To create it, pass your root reducing function to createStore.

//rootReducer object come from  index.js in the reducers folder we used the combineReducers to combine all our reducers into a single object so that is the rootReducer

//We have a config folder of react hidden in the main directory ,u can eject it to see it but better not to eject ,in that folder we have a file called env.js where the environment variables are defined.we can add our environment variables there too there is a variable in there called 'NODE_ENV' and it is set to development ,if u r in development mode so we need to access to that variable to only set the redux extension 'window._REDUX_DEVTOOLS_EXTENSION_COMPOSE_'  if we are in development mode so we use the default global variable called 'process' to access the environment variable 'NODE_ENV'.Below We say if we are in the development mode then set the redux_devtools to the variable 'composeEnhancers' if we are not in development mode ':' then set the 'NODE_ENV' to null and if the first case is null before the or || then we use the second case after or so we run compose if we get null in the first comparison before or .SO AS A result now redux dev toll will only be available in the development environment

const composeEnhancers =
  process.env.NODE_ENV === "development" &&
  window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__
    ? window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__({})
    : compose;

const enhancer = composeEnhancers(applyMiddleware(thunk));

// 'createStore' Creates a Redux store that holds the state tree. The only way to change the data in the store is to call dispatch() on it.There should only be 1 store

//Creates a store enhancer that applies middleware to the dispatch method of the Redux store. This is handy for a variety of tasks, such as expressing asynchronous actions in a concise manner, or logging every action payload.See redux-thunk package as an example of the Redux middleware.Because middleware is potentially asynchronous, this should be the first store enhancer in the composition chain.
//If we did not want to use the chorome dev tools we would Normally do: createStore(reducer,applyMiddleware(thunk))   but in our case we use the 'window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__' as the enhancer and we use applyMiddleware inside the enhanccer
const store = createStore(rootReducer, enhancer);

export default store;
