const initialState = {
  profile: null,
  profiles: null,
  //this is for showing a spinner animation ,if the loading to fetch the user info has not finished yet
  loading: false
};

export default function(state = initialState, action) {
  switch (action.type) {
    case "":
      return {
        ...state,
        profiles: action.payload,
        loading: false
      };

    default:
      return state;
  }
}
