import React, { Component } from "react";
//Browser Router mimic just a standart server like when we press back it goes to the page where we were,BELOW WE DID 'as Router' u do not have to do this but we did this so that we can use the Router tag instead of BrowserRouter in our code,it is a matter of preference ,u dont have to do it
//when we wanna create an individual Route we will use Route so we import Route as well
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";
//this is to decode the hashed token
import jwt_decode from "jwt-decode";
import setAuthToken from "./utils/setAuthToken";

import { setCurrentUser, logoutUser } from "./actions/authActions";

import { clearCurrentProfile } from "./actions/profileActions";

//Provider ,provides our application with the store ,Which holds the state.We use "react-redux" object ,in order to make redux store available in react we need a redux component called 'provider',the providers wraps the entire react app and passes the store as a props to react components
import { Provider } from "react-redux";
import store from "./store";

import NavBar from "./components/layout/Navbar";
import MainPage from "./components/layout/MainPage";
import About from "./components/layout/About";
import Footer from "./components/layout/Footer";
import Crew from "./components/layout/Crew";
import Gallery from "./components/layout/Gallery";
import Contact from "./components/layout/Contact";
import Register from "./components/auth/Register";
import Login from "./components/auth/Login";
import Dashboard from "./components/dashboard/Dashboard";
import CreateProfile from "./components/create-profile/CreateProfile";
import EditProfile from "./components/edit-profile/EditProfile";
import "./css/App.css";

class App extends Component {
  render() {
    return (
      <Provider store={store}>
        <Router>
          <div>
            <NavBar />
            <Route exact path="/" component={MainPage} />
            <Route exact path="/about" component={About} />
            <Route exact path="/crew" component={Crew} />
            <Route exact path="/gallery" component={Gallery} />
            <Route exact path="/contact" component={Contact} />
            <Footer />
          </div>
        </Router>
      </Provider>
    );
  }
}

export default App;
