const initialState = {};

export default function(state = initialState, action) {
  switch (action.type) {
    case "":
      //in authActions.js in actions folder we return errors.each reducer defined in index.js, in reducers folder has its own properties return from their own files so this errorReducer returns only an object for the errors reducer defined in the index.js in reducers folder you can see in the Redux tab installed in the chrome browser that we have different reducers defined in index.js in reducers folder each are independent from each other and have their own state values
      return action.payload;

    default:
      return state;
  }
}
