import { combineReducers } from "redux";

import authReducer from "./authReducer";
import errorReducer from "./errorReducer";
import profileReducer from "./profileReducer";
import postReducer from "./postReducer";

//combineReducers takes an object with our reducers,in store.js we import this 'combineReducers' as rootReducer so we can add this combineReducers into the store in store.js and use them/all the reducers added below in the store.And in the redux tab in the chrome browser under State ,u will see all these reducers ,each reducer will have their values returned from their INDIVIDUAL FILES like when we DO return{...state},they are all seperated
export default combineReducers({
  //if u wanna call an action or get the properties we will do this.props.auth
  auth: authReducer,
  errors: errorReducer,
  profile: profileReducer,
  post: postReducer
});
